@import "../../node_modules/@glidejs/glide/src/assets/sass/glide.core"
@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@100;200;300;400;500;600;700;800&display=swap"    
@import 'preset'
.nav__container 
    margin-top: -50px
    position: fixed
    z-index: 900
    width: 100%
    background-color: $black
    font-family: 'Montserrat', sans-serif
.nav__list
    display: flex
    justify-content: space-around
    max-width: $maxcontainer
    height: 50px
    margin-left: auto
    margin-right: auto
.nav__item
    margin: auto 0
    vertical-align: middle
    text-decoration: none
    color: $white
    font-size: 24px
    transition: 400ms ease
@media (hover: hover) 
.nav__item:hover
    flex-basis: 2



.cover__container
    margin: 0 auto
    display: flex
    flex-direction: column
    width: 100%
    max-width: $maxcontainer
.cover__h1
    text-align: center
    font-size: 24px
    padding: 51px 0px 42px
    margin: 0 auto
    font-weight: 400
.cover__h1_strong
    font-weight: 500
.cover__description
    font-weight: 400
    padding: 0px 88px
    font-size: 24px
    margin: 0px auto
    text-align: justify
.cover__btn
    font-size: 24px
    padding: 20px 100px
    color: black
    background-color: $gray
    text-decoration: none
    border: none
    outline: none
    margin: 29px auto 47px

html
    padding-top: 50px
    
.gallery__slider
    max-width: $maxcontainer
    padding: 20px 16px
    border-top: 1px solid 
    border-bottom: 1px solid 
    border-image: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgb(16, 16, 16)50%, rgb(255, 255, 255) 100%)
    border-image-slice: 1
    animation: gradient 700ms ease
    margin: 0px auto 47px
.gallery__item 
    height: auto

.price__list
    margin: 0 18px 49px
.price__h2 
    font-size: 48px
    font-weight: 400
    margin: 0 auto 56px
    text-align: center
.price__item
    padding: 25px 20px 20px
    margin-left: auto 
    margin-right: auto
    max-width: $maxcontainer
    background: rgb(255,255,255)
    background: linear-gradient(144deg, rgba(255,255,255,1) 0%, rgba(196,196,196,1) 100%)      
    display: flex
    justify-content: space-between
.price__item:not(:last-child) 
    margin-bottom: 13px
.price__service
    font-size: 18px
.price__description
    font-size: 13px
.price__underline
    display: flex
    align-items: center
    height: 30px
.price__cost
    font-size: 24px
    font-weight: 400 
.price__number
    font-weight: 500 




.masters__container
    max-width: $maxcontainer
    margin: 0px auto 62px
    padding: 0px 31px
    min-height: 200px
    display: flex
    justify-content: space-around  
    align-items: center
    position: relative  
.masters__leftheader
    left: 0
    position: absolute
    display: inline
    height: 26px
    font-size: 26px
    text-align: center
    color: black
    // text-shadow: 0px 0px 11px rgba(28, 12, 217, 0.25)
    transform: rotate(-90deg)
.masters__item
    overflow: hidden
    color: $white
    position: relative
    width: 200px
    height: 200px
    padding-bottom: auto 
    border-radius: 10px
    background-color: green
.masters__photo
    top: 0
    left: 0
    position: absolute
    width: 100%
    height: auto
    transition: 600ms ease
.masters__photo:hover
    transform: scale(1.1)
.masters__name
    font-weight: 700
    font-size: 14px
    height: 15px
    position: absolute
    bottom: 16px
    left: 16px
.contacts__container
    padding: 31px 28px
    margin: 0 auto
    max-width: $maxcontainer
.contacts__p
    padding: 35px 17px
    border: 1px solid $black
    color: $black
    font-weight: 400
    text-align: center

.pool__container
    position: relative
    overflow: hidden
    height: 36px
.pool__img
    left: 0
